

  .container{
    margin: auto;
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:2%;
        
  }
  
  .cardContainer{
    background-color:  #184218;
    border: 2px solid rgb(214,232,182);
    width: 300px;
    height: 200px;
    display: grid;
    grid-template-columns: 200px 100px;
     
  }
  a{
    font-size: 0.5rem;
    color: honeydew;
  }
  ul{
    height: 50px;
  }
  .list{
      direction: rtl;
      text-align: right;
      word-wrap: break-word;
      font-size: 0.8rem;
  }
  .send{
    position: relative;
    left: 5px;
    bottom: 5px;
    height: 2rem;
    width: 6rem;
    
  }
  .photo{
      height: 100px;
  }
  .seek, .field{
    font-size: 1rem;
    margin: 22px;

  }
  .cityArea{
    font-size: 1.1rem;
    padding: 0;
  }
  .platoon{
    font-size: 1rem;
    padding: 0;
  }

  @media only screen and (max-width: 600px) {
    .container {
      margin: auto;
      display: inline;
      
  }

  .App {
    text-align: center;
    margin: auto;
    width: 100%;
    background: rgb(3, 102, 27);
  }
}