.App {
  text-align: center;
  margin: auto;
  width: 80%;
  background: rgb(3, 102, 27);
}
.search > button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  height: 3rem;
  border: 3px solid black;
  border-radius: 8px;
  width: 10rem;
}
.search{
  direction: rtl;
  display: flex;
}
.search .contactButton{
  position: relative;
  right:  0px;
  background-color: rgb(14, 3, 109); 
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  height: 3rem;
  border: 3px solid black;
  border-radius: 8px;
  width: 10rem;
}
.search > form{
  align-items: center;
}
  

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1c791c;
  /* min-height: 100vh; */
  /*display: flex;
  flex-direction: column; */
  margin-top: 1rem;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  height: 100%;
}

.App-link {
  color: #61dafb;
}

