.form{
    border: 2px solid green;
    width: 30%;
    text-align: center;   
    position: relative; 
    direction: rtl;
    left: 30%;
    background-color: honeydew;
}
.text{
    height: 15rem;
    width: 70%;
}

@media only screen and (max-width: 600px) {
.form{
    width: 100%;
    left: 0px;
}
}