.headContainer{
    background-color: #184218;
    /* min-height: 50vh; */
    display: grid;
    grid-template-columns: 1fr 1fr 2fr 0.5fr;
}

.headContainer > img{
    position: relative;
    left: 200px;
    /* top: 50px; */
    width: 10rem;
}

.speach{
        color: white;
        text-align: right;
        direction: rtl;
}
.speach >h2{
        margin-top: 4rem;
}

@media only screen and (max-width: 600px){
    .headContainer{
        background-color: #184218;
        min-height: 50vh;
        display: grid;
    grid-template-columns: 0.2fr 1fr 0.2fr
    }
    .headContainer > img{
        position: absolute;
        left: -200px;
        top: -100px;
        width: 10rem;
    }
}